@import 'variables.scss';
@import 'mixins.scss';

/*!
 * UXWorks.design - v1.0.0-beta (http://www.uxworks.design)
 * Copyright 2016-2017 UXWorks
 */

body {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
/*  position: relative;*/
  width: 100%;
  height: 100%;
  color: $primary;
}

::selection {
  background: #F7F9FB;
}
::-moz-selection {
  background: #F7F9FB;
}

::-webkit-scrollbar {
display: none;
}

html {
  width: 100%;
  height: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 700;
  margin: 0 0 35px;
  letter-spacing: 1px;
}

p {
  font-size: 16px;
}

@media (min-width: 768px) {
  p {
    font-size: 18px;
  }
}

a {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: $primary;
  &:focus, &:hover {
    text-decoration: none;
  }
}

#mainNav {
  .navbar-toggler {
    font-size: 16px;
    padding: 11px;
    color: white;
  }
  .navbar-brand {
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
  }
  a {
    color: white;
    font-size: 1.25rem;
  }
  .navbar-nav .nav-item {
    -webkit-transition: background 0.3s ease-in-out;
    -moz-transition: background 0.3s ease-in-out;
    transition: background 0.3s ease-in-out;
    &:hover {
      color: fade(white, 80%);
      outline: none;
      background-color: transparent;
    }
    &:active, &:focus {
      outline: none;
      background-color: transparent;
    }
  }
}

@media (min-width: 400px) {
  #mainNav {
    padding-top: 40px;
    padding-bottom: 40px;
    -webkit-transition: background 0.3s ease-in-out, padding-top 0.3s ease-in-out, padding-bottom 0.3s;
    -moz-transition: background 0.3s ease-in-out, padding-top 0.3s ease-in-out, padding-bottom 0.3s;
    transition: background 0.3s ease-in-out, padding-top 0.3s ease-in-out, padding-bottom 0.3s;
    letter-spacing: 1px;
    background: transparent;
    &.navbar-shrink {
      padding-top: 20px;
      padding-bottom: 20px;
      background: #d32323;
    }
    .nav-link.active {
      outline: none;
      background-color: rgba(255, 255, 255, 0.3);
      &:hover {
        color: white;
      }
    }
  }
}

.navbar-brand img {
  height: 22px;
  width: auto;
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.navbar-shrink img {
  height: 18px;
  width: auto;
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.masthead {
  width: 100vw;
  height: auto;
  color: $primary;
  background-image: url('../img/header.jpg');
  background-size: cover;
}

@media (min-width: 400px) {
  .masthead {
    width: 100%;
    height: 60%;
  }
}


@media (min-width: 768px) {
  .masthead {
    height: 100%;
    padding: 0;
    .intro-body {
      .brand-heading {
        font-size: 100px;
      }
      .intro-text {
        font-size: 18px;
      }
    }
  }
}

.content-header {
  padding: 120px 0 100px 0;
  position: relative;
}

.btn {
  font-family: 'Cabin', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  border-radius: 8px;
  width: 100%;
  height: 50px;
  margin: 20px;
  cursor: pointer
}

.btn-default {
  color: #42DCA3;
  border: 1px solid #42DCA3;
  background-color: transparent;
  &:focus, &:hover {
    color: black;
    border: 1px solid #42DCA3;
    outline: none;
    background-color: #42DCA3;
  }
}

#think {
  height: 400px;
  img {
    width: auto;
    height: 400px;
  }
}

#works {
  color: $primary;
}

.greybg {
  width: 23%;
  display: block;
  height: 630px;
  background-color: #f7f9fb;
}

.greybg-right {
  position: absolute;
  right: 0px;
  width: 12%;
  height: 327px;
  display: block;
  background-color: #f7f9fb;
}

.btn-header {
  border-radius: 4px;
  color: $primary;
  font-size: 16px;
  font-family: Open Sans, Helvetica;
  padding: 13px 18px;
  text-transform: none;
  border-color: $primary;
  &:hover {
    background-color: $primary;
    border-color: $primary;
  }
}

.cards {
  width: 100%;
  overflow: auto;
  white-space: nowrap;
  margin-top: 20px;
  margin-bottom: 20px;
}

.card-item {
  display: inline-block;
  margin-left: 15px;
}

.category {
  transition: all .3s ease-in-out;
  -webkit-transition: all .3s ease-in-out;
  position: absolute;
  width: 100vw;
  div {
    display: inline-block;
    vertical-align: middle;
  }
  .btn {
    font-size: 20px;
    font-family: 'Gochi Hand';
    text-transform: none;
    padding: 6px 12px;
    background: transparent;
    cursor: pointer;
  }
}

.unselected {
  text-decoration: line-through;
  color: $primary;
}

.selected {
  text-decoration: none;
  color: $theme;
}

.category button:hover {
  color: #5e5e5e;
  border-color: #5e5e5e;
}

.category-title {
  margin: 0 20px;
  transition: all .2s ease-in-out;
  -webkit-transition: all .2s ease-in-out;

  h4 {
    font-size: 28px;
    font-family: Roboto Slab;
    font-weight: 300;
    margin-bottom: 5px;
  }
}

.title-delay {
  -webkit-transition-delay: .15s;
  transition-delay: .15s;
}

.work-title {
  margin: 100px 0;
  font-style: italic;
  font-size: 24px;
  margin-bottom: 10px;
}

.work-subtitle {
  font-size: 12px;
  color: #A3A5A9;
  margin-bottom: 10px;
}

.social-icons {
  display: inline;
  i {
    margin: 10px;
  }
}

.uxwork {
  padding-top: 100px;
  // padding-bottom: 80px;
}

.div-line {
  height: 2px;
  width: 100%;
  margin-top:100px;
  background-color:#F7F9FB;
}

.parallax-bg {
  // position: absolute;
  margin-left: 15px;
  height: 220px;
  width: auto;
  right: -165px;
  top: 0;
  z-index: -1;
}

.hero-text p {
  font-family: 'Merriweather', serif;
  font-size: 36px;
}

.hero-text {
  width: 750px;
  padding-left: 15px;
  padding-top: 10px
}

.hero-links {
  color: #8A8E93;
  transition: .25s ease-in-out;
  -webkit-transition: .25s ease-in-out;
  &:hover {
    color: $theme;
  }
}

.nav-icons a {
  padding: 10px;
}

#title {
  margin: 50px 0;
}

.divider {
  width: 100vw; 
  height: 15px; 
  background-color: rgba(0,0,0,.02);
  border-top: 1px solid rgba(0,0,0,.1);
  border-bottom: 1px solid rgba(0,0,0,.1);
  p {
    margin: 15px 0;
  }
}

#actions {
  margin-bottom: 20px;
}

.rate {
  margin: 20px 0;
  img{
    width: 30px;
    height: auto;
    margin-right: 1px;
  }
}

.rating {
  margin: 10px auto;
  padding-top: 20px;
  img{
    width: 50px;
    height: auto;
    margin-right: 4px;
    cursor: pointer;
    &:hover {
    opacity: .8;
    }
  }
}

.action-btn{
  cursor: pointer;
  &:hover {
    opacity: .8;
  }
  i {
  font-size: 1.5rem;
  margin-top: 15px;
  }
}

.action-border {
  border-right: 1px solid rgba(0,0,0,.1);
}

.google-maps {
  position: relative;
  padding-bottom: 60vh;
  height: 0;
  overflow: hidden;
}

.google-maps iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 60vh !important;
}

.address {
  margin: 25px 0 0 0;
  p {
    margin: 0;
  }
}

.grey-text {
  font-size: 15px;
  color: grey;
}

.inlineblock {
  display: inline-block;
}

.icon-side { 
  i {
  font-size: 1.75rem;
  padding: 20px;
  }
}

#comments {
  .row {
    padding: 25px 10px 0 10px;
  }
  img {
    width: 100%;
    height: auto;
  }
}